"use client";

import { useEffect, useState, useCallback } from "react";
import api from "@/lib/axios";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import {
    DropdownMenu,
    DropdownMenuContent,
    DropdownMenuItem,
    DropdownMenuTrigger
} from "@/components/ui/dropdown-menu";
import { ChevronDown, MoreVertical, Search, Filter } from "lucide-react";
import { Input } from "@/components/ui/input";
import { Badge } from "@/components/ui/badge";
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog";

interface AdminOrder {
    id: number;
    reference: string;
    recipient_phone: string;
    status: string;
    cost: number;
    user?: {
        email: string;
    };
    bundle?: {
        name: string;
    };
    created_at: string;
}

export default function AdminOrdersPage() {
    const [orders, setOrders] = useState<AdminOrder[]>([]);
    const [filter, setFilter] = useState("all");
    const [search, setSearch] = useState("");
    const [loading, setLoading] = useState(true);
    const [selectedOrder, setSelectedOrder] = useState<AdminOrder | null>(null);

    const fetchOrders = useCallback(async () => {
        setLoading(true);
        try {
            const url = filter === 'all' ? '/admin/orders' : `/admin/orders?status=${filter}`;
            const res = await api.get(url);
            setOrders(res.data.data);
        } catch (e) {
            console.error(e);
        } finally {
            setLoading(false);
        }
    }, [filter]);

    useEffect(() => {
        fetchOrders();
    }, [fetchOrders]);

    const updateStatus = async (id: number, status: string) => {
        try {
            setOrders(orders.map(o => o.id === id ? { ...o, status } : o)); // Optimistic update
            await api.put(`/admin/orders/${id}`, { status });
            fetchOrders(); // Refresh to be safe
        } catch (e) {
            alert("Failed to update status");
            fetchOrders(); // Revert
        }
    };

    const getStatusColor = (status: string) => {
        switch (status) {
            case 'completed': return 'bg-green-100 text-green-700 hover:bg-green-100 dark:bg-green-900/30 dark:text-green-400';
            case 'pending': return 'bg-orange-100 text-orange-700 hover:bg-orange-100 dark:bg-orange-900/30 dark:text-orange-400';
            case 'failed': return 'bg-red-100 text-red-700 hover:bg-red-100 dark:bg-red-900/30 dark:text-red-400';
            case 'processing': return 'bg-blue-100 text-blue-700 hover:bg-blue-100 dark:bg-blue-900/30 dark:text-blue-400';
            default: return 'bg-slate-100 text-slate-700 hover:bg-slate-100 dark:bg-slate-800 dark:text-slate-400';
        }
    };

    const filteredOrders = orders.filter(o =>
        o.reference.toLowerCase().includes(search.toLowerCase()) ||
        o.user?.email.toLowerCase().includes(search.toLowerCase()) ||
        o.recipient_phone.includes(search)
    );

    return (
        <div className="space-y-6">
            <div className="flex flex-col sm:flex-row items-center justify-between gap-4">
                <h2 className="text-2xl font-bold tracking-tight text-slate-800 dark:text-slate-100">Manage Orders</h2>
                <div className="flex items-center gap-2 w-full sm:w-auto">
                    <div className="relative flex-1 sm:w-64">
                        <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-slate-400" />
                        <Input
                            placeholder="Search orders..."
                            className="pl-9"
                            value={search}
                            onChange={(e) => setSearch(e.target.value)}
                        />
                    </div>
                    <DropdownMenu>
                        <DropdownMenuTrigger asChild>
                            <Button variant="outline" className="gap-2">
                                <Filter className="w-4 h-4" /> Filter <ChevronDown className="w-4 h-4" />
                            </Button>
                        </DropdownMenuTrigger>
                        <DropdownMenuContent align="end">
                            <DropdownMenuItem onClick={() => setFilter('all')}>All Statuses</DropdownMenuItem>
                            <DropdownMenuItem onClick={() => setFilter('pending')}>Pending</DropdownMenuItem>
                            <DropdownMenuItem onClick={() => setFilter('processing')}>Processing</DropdownMenuItem>
                            <DropdownMenuItem onClick={() => setFilter('completed')}>Completed</DropdownMenuItem>
                            <DropdownMenuItem onClick={() => setFilter('failed')}>Failed</DropdownMenuItem>
                        </DropdownMenuContent>
                    </DropdownMenu>
                </div>
            </div>

            <Card className="border-none shadow-[0_2px_10px_0_rgba(67,89,113,0.12)]">
                <div className="rounded-md">
                    <div className="w-full overflow-x-auto">
                        <table className="w-full text-sm text-left">
                            <thead className="text-xs uppercase bg-slate-50 dark:bg-slate-900 text-slate-500 font-semibold">
                                <tr>
                                    <th className="px-6 py-4">Reference</th>
                                    <th className="px-6 py-4">User</th>
                                    <th className="px-6 py-4">Bundle</th>
                                    <th className="px-6 py-4">Phone</th>
                                    <th className="px-6 py-4">Cost</th>
                                    <th className="px-6 py-4">Status</th>
                                    <th className="px-6 py-4 text-right">Actions</th>
                                </tr>
                            </thead>
                            <tbody className="divide-y divide-slate-100 dark:divide-slate-800">
                                {loading ? (
                                    <tr><td colSpan={7} className="px-6 py-8 text-center text-slate-500">Loading...</td></tr>
                                ) : filteredOrders.length === 0 ? (
                                    <tr><td colSpan={7} className="px-6 py-8 text-center text-slate-500">No orders found.</td></tr>
                                ) : (
                                    filteredOrders.map((order) => (
                                        <tr key={order.id} className="hover:bg-slate-50/50 dark:hover:bg-slate-800/50 transition-colors">
                                            <td className="px-6 py-4 font-medium text-primary">#{order.reference}</td>
                                            <td className="px-6 py-4">
                                                <div className="flex flex-col">
                                                    <span className="font-medium">{order.user?.email || 'Guest'}</span>
                                                </div>
                                            </td>
                                            <td className="px-6 py-4">{order.bundle?.name || 'Unknown'}</td>
                                            <td className="px-6 py-4 font-mono text-slate-500">{order.recipient_phone}</td>
                                            <td className="px-6 py-4 font-semibold">GHS {order.cost}</td>
                                            <td className="px-6 py-4">
                                                <Badge className={`${getStatusColor(order.status)} border-none shadow-none`}>
                                                    {order.status}
                                                </Badge>
                                            </td>
                                            <td className="px-6 py-4 text-right">
                                                <DropdownMenu>
                                                    <DropdownMenuTrigger asChild>
                                                        <Button variant="ghost" size="icon" className="h-8 w-8 p-0">
                                                            <MoreVertical className="h-4 w-4" />
                                                        </Button>
                                                    </DropdownMenuTrigger>
                                                    <DropdownMenuContent align="end">
                                                        <DropdownMenuItem onClick={() => setSelectedOrder(order)}>
                                                            View Details
                                                        </DropdownMenuItem>
                                                        <DropdownMenuItem onClick={() => updateStatus(order.id, 'completed')}>
                                                            Mark as Completed
                                                        </DropdownMenuItem>
                                                        <DropdownMenuItem onClick={() => updateStatus(order.id, 'processing')}>
                                                            Mark as Processing
                                                        </DropdownMenuItem>
                                                        <DropdownMenuItem className="text-red-500 focus:text-red-500" onClick={() => updateStatus(order.id, 'failed')}>
                                                            Mark as Failed
                                                        </DropdownMenuItem>
                                                    </DropdownMenuContent>
                                                </DropdownMenu>
                                            </td>
                                        </tr>
                                    ))
                                )}
                            </tbody>
                        </table>
                    </div>
                </div>
            </Card>

            <Dialog open={!!selectedOrder} onOpenChange={(open) => !open && setSelectedOrder(null)}>
                <DialogContent>
                    <DialogHeader>
                        <DialogTitle>Order Details</DialogTitle>
                    </DialogHeader>
                    {selectedOrder && (
                        <div className="grid gap-4 py-4">
                            <div className="grid grid-cols-4 items-center gap-4">
                                <span className="font-bold text-slate-500">Reference:</span>
                                <span className="col-span-3 font-mono">{selectedOrder.reference}</span>
                            </div>
                            <div className="grid grid-cols-4 items-center gap-4">
                                <span className="font-bold text-slate-500">User:</span>
                                <span className="col-span-3">{selectedOrder.user?.email || 'N/A'}</span>
                            </div>
                            <div className="grid grid-cols-4 items-center gap-4">
                                <span className="font-bold text-slate-500">Bundle:</span>
                                <span className="col-span-3">{selectedOrder.bundle?.name || 'N/A'}</span>
                            </div>
                            <div className="grid grid-cols-4 items-center gap-4">
                                <span className="font-bold text-slate-500">Recipient:</span>
                                <span className="col-span-3">{selectedOrder.recipient_phone}</span>
                            </div>
                            <div className="grid grid-cols-4 items-center gap-4">
                                <span className="font-bold text-slate-500">Cost:</span>
                                <span className="col-span-3 font-semibold">GHS {selectedOrder.cost}</span>
                            </div>
                            <div className="grid grid-cols-4 items-center gap-4">
                                <span className="font-bold text-slate-500">Status:</span>
                                <span className="col-span-3">
                                    <Badge className={`${getStatusColor(selectedOrder.status)}`}>{selectedOrder.status}</Badge>
                                </span>
                            </div>
                            <div className="grid grid-cols-4 items-center gap-4">
                                <span className="font-bold text-slate-500">Date:</span>
                                <span className="col-span-3 text-sm text-slate-500">{new Date(selectedOrder.created_at).toLocaleString()}</span>
                            </div>
                        </div>
                    )}
                </DialogContent>
            </Dialog>
        </div>
    );
}
